import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import { IonApp, IonRouterOutlet } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import Home from './pages/Home';
import Functions from './pages/Functions';
import FileTop from './pages/FileTop';
import ChatTop from './pages/ChatTop';
import ChatRoom from './pages/ChatRoom';
import FileSend from './pages/FileSend';
import FileReceive from './pages/FileReceive';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

const App: React.FC = () => (
  <IonApp>
    <IonReactRouter>
      <IonRouterOutlet>
        <Route path="/home" component={Home} />
        <Route path="/functions" component={Functions} />
        <Route path="/fileTop" component={FileTop} />
        <Route path="/ChatTop" component={ChatTop} />
        <Route path="/ChatRoom" component={ChatRoom} />
        <Route path="/FileSend" component={FileSend} />
        <Route path="/FileReceive" component={FileReceive} />
        <Redirect exact from="/" to="/home" />
      </IonRouterOutlet>
    </IonReactRouter>
  </IonApp>
);

export default App;
